@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital@1&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital@1&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital@1&family=Poppins&display=swap);
/* Google Fonts */


:root {
  --primary: #034963;
  --secondary: #447b9e;
  --tertiary: #3a0d6f;
  --light: #e8f8f8;
  --dark: #00080b;
  --error: rgb(56, 38, 33);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  background-color: white;
  font-family: 'Poppins', sans-serif;
}

body {
  margin: 0;
  padding-bottom: 50px;
  font-family: 'Poppins', sans-serif;
  font-weight: 350;
  line-height: 2.5;
  font-size: 25px;
  color: #00080b;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Poppins', sans-serif;
  font-weight: 350;
  margin: 9px 0;
  line-height: 1.6;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #3a0d6f;
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;

}

a:hover {
  text-decoration: underline;
}

/* Header */

header {
  background: #034963;
  background: var(--primary);
  height: 80px
}

header a {
  color: #e8f8f8;
  color: var(--light);
}
header li {
  text-decoration: none;
  list-style: none;
}
header img {
  max-width: 25%;
  background-color: #e8f8f8;
  background-color: var(--light);
}

/* Slider */

img, svg {
  padding-top: 10px;

}

/* Footer */


/* Button */

button {
  font-size: 1.5rem;
  text-decoration: none;
  font-style: #e8f8f8;
  font-style: var(--light);
  -webkit-text-decoration-color: #e8f8f8;
          text-decoration-color: #e8f8f8;
  color:white;
  padding: 0.25rem 1.5rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1.0px;
  background-color: #447b9e;
  background-color: var(--secondary);
}

button:hover {
  opacity: .9;
}

button[disabled] {
  cursor: default;
  opacity: .9;
}





img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

/*  Form*/

form {
  width: 100%;
  background-color: white;
}

input {
  font-size: 1rem;
  /* padding: 0.25rem; */
  padding-left: 20rem;
  border: 2px solid #00080b;
  border: 2px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

/* Form Input */

input:focus {
  border: 3px solid #00080b;
  border: 3px solid var(--dark);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}
/* Contact Form input*/
.formRow {
  margin: 10px 0 20px 0;
}
.formInput {
  padding: 15px 10px;
  background-color: var(--whitesmoke);
  box-shadow: 0 0 0 2px #00080b;
  box-shadow: 0 0 0 2px var(--dark);
  border: 10px;
  color: var(--blue);
}
.formInput:focus {
  background-color: var(--grey);
  outline: none;
  box-shadow: 0 0 0 2px #00080b;
  box-shadow: 0 0 0 2px var(--dark);
  color: #00080b;
  color: var(--dark);
}

.formInput::-webkit-input-placeholder {
  color: #00080b;
  color: var(--dark);
  opacity: 1;
}

.formInput::placeholder {
  color: #00080b;
  color: var(--dark);
  opacity: 1;
}

.formInput:focus::-webkit-input-placeholder {
  color: #00080b;
  color: var(--dark);
  opacity: 1;
}

.formInput:focus::placeholder {
  color: #00080b;
  color: var(--dark);
  opacity: 1;
}

.errorMessage {
  color: #f45532;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

#notifyToast {
  color: #fff;
  border-radius: 6px;
  width: 200px;
  margin-left: 75px;
  background-color: #00080b;
  background-color: var(--dark);
}

/* Container */

.container {
  width: 85%;
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

/*  card*/

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: rgb(56, 38, 33);
  color: var(--error);
}

/* Media Screen */


@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}
/* Media Screen - Contact */
@media (max-width: 768px) {
  .submit-message {
    width: 125px;
    margin-left: 200px;
  }
}
/* Media Screen Nav */
@media screen and (max-width: 992px) {
  nav {
     font-size: medium;
  }
}

/* Media Screen Header */
@media screen and (max-width: 992px) {
  header a {
     font-size: medium;
  }
}
.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: var(--light);
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-bottom-left-radius: .5rem;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}
.cart-closed {
  position: fixed;
  top: 2%;
  right: 1%;
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: fixed;
    margin: 0 auto;
  }
}
footer {

    width: 100%;
    height: 60px;
    text-align: center;
    margin-top: 5rem;  
    justify-content: center;
    display: inline-flex;
    font-weight: 400;
    flex-wrap: wrap;
    font-size: 10px;
    padding-top: 10px;
    padding-bottom: 25px;
    flex-shrink:0;
  
  
  }
